import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "home",
      component: () => import("./views/Home.vue"),
      meta: {
        title: "节点工作室欢迎您"
      }
    },
    {
      path: "/solutions",
      name: "solutions",
      component: () => import("./views/Solutions.vue"),
      meta: {
        title: "Solutions"
      }
    },
    {
      path: "/services",
      name: "services",
      component: () => import("./views/Services.vue"),
      meta: {
        title: "Services"
      }
    },
    {
      path: "/cases",
      name: "cases",
      component: () => import("./views/Cases.vue"),
      meta: {
        title: "Cases"
      }
    },
    {
      path: "/tenet",
      name: "tenet",
      component: () => import("./views/Tenet.vue"),
      meta: {
        title: "Tenet"
      }
    },
    {
      path: "/about",
      name: "about",
      component: () => import("./views/About.vue"),
      meta: {
        title: "About"
      }
    }
  ]
});

router.afterEach(to => {
  window.scrollTo(0, 0);
});
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});
export default router;
