<template>
  <section class="go2top" @click="go2Top" v-if="isTop">
    <img src="../assets/images/ic-go2top.png" alt="返回顶部">
  </section>
</template>

<script>
export default {
  name: "Go2top",
  data() {
    return {
      isTop: false
    };
  },

  mounted() {
    this.needScroll();
  },
  methods: {
    go2Top() {
      // let timer = setInterval(function() {
      //   let osTop =
      //     document.documentElement.scrollTop || document.body.scrollTop;
      //   let ispeed = Math.floor(-osTop / 5);
      //   document.documentElement.scrollTop = document.body.scrollTop =
      //     osTop + ispeed;
      //   if (osTop === 0) {
      //     clearInterval(timer);
      //   }
      // }, 30);
      window.scroll({ top: 0, left: 0, behavior: "smooth" });
    },
    needScroll() {
      let clientHeight = document.documentElement.clientHeight;

      window.onscroll = () => {
        let osTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        this.isTop = osTop >= clientHeight;
      };
    }
  }
};
</script>

<style scoped lang="scss">
.go2top {
  position: fixed;
  right: 1%;
  bottom: 1%;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 10%;
  z-index: 999;
  transition: all 0.5s ease;
  text-align: center;
  vertical-align: middle;
  background: #41b37d99;
  padding: 0.06rem;
  img {
    margin: auto 0;
    width: 0.28rem;
    height: 0.28rem;
  }
}
.go2top:hover {
  border-radius: 50%;
  background: #41b37d99;
}
</style>
