import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import { Tabs, TabPane, Carousel, CarouselItem } from "element-ui";

Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Carousel);
Vue.use(CarouselItem);

Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");

// 初始化rem适配，注意由于浏览器有最小字体限制，根据测试html上的字体设置不能低于9.6，在设置的时候尽量保持大于10
function initRem() {
  let clientWidth = document.body.clientWidth;
  let pcDesignWidth = 1920;
  let mobilDesignWidth = 375;
  let rem = 10;
  if (clientWidth >= 1200) {
    rem = 100;
  } else if (clientWidth >= mobilDesignWidth * 2) {
    rem = (clientWidth / pcDesignWidth) * 100;
  } else if (clientWidth >= mobilDesignWidth) {
    //之所以除以10，是因为浏览器有最小字体限制，如果最后设置的太小会没有效果，因此在设计稿的宽度里除以10，这样html设置的字体大小就会放大10倍
    rem = (clientWidth / mobilDesignWidth) * 10;
  }
  document.documentElement.style.fontSize = rem + "px";
}
window.addEventListener("load", initRem);
window.addEventListener("resize", initRem);
